.form {
  padding: 40px 30px 30px;
  max-width: 380px;
  box-sizing: border-box;
  background: white;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);

  &__inner {
    border: none;
    padding: 0;
    margin: 15px 0;

    &_search-room {
      margin-bottom: 30px;
    }
    &_log .input {
      margin: 5px 0;
    }
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 30px 0 0;
  }
}